<template>
    <div class="hot">
        <TopNav v-show="!this.$root.playingMusic.isFullscreenShow"></TopNav>
        <div class="hot-top">
            <div class="content">
                <div class="pic"></div>
                <div class="date">
                    更新日期：{{updateDate|formatDate}}
                </div>
            </div>
        </div>
        <MusicItem :MusicList="hotMusicList"></MusicItem>
        <Loading v-if="isLoading"></Loading>
        <div class="more" v-show="hotMusicList.length>0">
            <a @click="abc()">查看更多</a>
        </div>
    </div>
</template>
<script>
import TopNav from '../components/TopNav'
import Loading from '../components/Loading'
import MusicItem from '../components/MusicItem'
export default {
    data(){
        return {
            hotMusicList:[],
            updateDate:new Date(),
            num:0,
            isLoading:true
        }
    },
    methods:{
        abc(){
            this.loadMusic(this);
        },
        loadMusic(vm){
            let musicids=[];
            vm.num++;
            vm.$http.get('top/list?idx=1').then((data)=>{
                //获取更新的时间
                vm.updateDate = data.data.playlist.updateTime;
                //获取音乐ID
                musicids = data.data.playlist.trackIds.slice(20*(vm.num-1),20*vm.num);
            }).then(()=>{ 
                for(let v of musicids){
                    //根据音乐ID获取音乐信息
                    vm.$http.get("song/detail?ids="+v.id).then((data)=>{
                        //自己设计对应的数据结构
                        vm.hotMusicList.push({
                            name:data.data.songs[0].name,
                            id:data.data.songs[0].id,
                            song:{
                                privilege:{
                                    maxbr:data.data.privileges[0].maxbr
                                },
                                artists:data.data.songs[0].ar
                            }
                        });
                    }).then(()=>{
                        //加载完成，关闭加载符号
                        vm.isLoading = false;
                    });
                }
            });
        }
    },
    components:{
        Loading,
        MusicItem,
        TopNav
    },
    beforeRouteEnter(to,from,next){
        next(vm=>{
            //初始化
            vm.hotMusicList = [];
            vm.num = 0;
            vm.loadMusic(vm);
        });
    },
    filters:{
        formatDate(value){
            let d = new Date(value);
            let month = (d.getMonth()+1);
            let day = d.getDate();
            return (month<10?'0'+month:month)+"月"+(day<10?'0'+day:day)+"日";
        }
    }
}
</script>

<style lang="less" scoped>
.hot {
    .hot-top {
        background: url('../assets/hot_music_bg_2x.jpg') no-repeat;
        background-size: 100% auto;
        padding:10% 0px 10% 20px;
        .content{
            .pic {
            width: 142px;
            height: 67px;
            background: url('../assets/play.png')no-repeat -24px -30px ;
            background-size: 166px 97px;
            }
            .date {
                margin-top: 10px;
                color: hsla(0,0%,100%,.8);
                font-size: 12px;
            }
        }
    }
    .more {
        height: 55px;
        text-align: center;
        a {
            line-height: 55px;
            text-decoration: none;
            color: #999999;
        }
    }
}

</style>